import { Component, OnInit } from '@angular/core';
import { DeviceService } from 'src/app/core/services/device.service';
import { Title } from '@angular/platform-browser';
import { IBreadcrumb } from 'src/app/shared/breadcrumb/breadcrumb.component';
import { MenuService } from 'src/app/core/services/header/menu.service';
import { Router } from '@angular/router';
import { GtmService } from 'src/app/core/services/gtm.service';
import { SeoService } from 'src/app/core/services/header/seo.service';

@Component({
  selector: 'app-lojas',
  templateUrl: './lojas.component.html',
  styleUrls: ['./lojas.component.scss']
})
export class LojasComponent implements OnInit {

  public isMobile: boolean;

  public periods = ['12', '18', '24'];

  public lojas: any = []

  public steps: IBreadcrumb[] = [
    {
      id: '0',
      label: 'Voltar para home',
      link: '/'
    },
    {
      id: '1',
      label: 'Lojas'
    }
  ];

  constructor(
    private titleS: Title,
    private device: DeviceService,
    private router: Router,
    private gtm: GtmService,
    private menuService: MenuService,
    private seoService: SeoService
  ) { }

  ngOnInit() {
    this.titleS.setTitle('Zero Km - Lojas');
    this.seoService.createLinkForCanonicalURL();
    this.isMobile = this.device.isMobile();
    this.gtm.addPageGTM('Pageview ZeroKM', this.router.url, 'Zero Km - Lojas');
    this.loadServices();
  }

  loadServices() {
    this.menuService.getLojas().subscribe(data => {
      this.lojas = data;
    }, error => console.log(error));
  }
}
